import React from 'react';
import {Card} from 'antd';

const paymentStatus ={
    1:"deme Bekliyor",
    2:"Onayland",
    4:"Onay Bekliyor"
};
const getOnline = (props) => {
    const {payment} = props;
    return (<Card
        title="Online deme Bilgileri"
        style={{width: 300}}
    >
        <p>
            Alnan cret : {payment.totalPrice} 
        </p>
        <p>
            Tarih : {payment.createdDate.split("T")[0]}
        </p>
        <p>
            Aklama : {payment.method.description}
        </p>
        <p>
            Durum : {paymentStatus[payment.method.status]}
        </p>
    </Card>);
};
export default getOnline;